<template>
  <div id="home">
    <scroll class="wrapper" ref="scroll" :probe-type="3" :listen-scroll="listenScroll"
            :pullup="true" :pulldown="true"
            @scroll="ContentScroll" @pullingUp="pullUp" @pullingDown="pullDown">
      <div class="content" style="padding-top: 20px;padding-bottom: 20px">
        <Row type="flex" justify="center" align="middle">
          <Col :xs="{span:23}" :sm="{span:23}" :lg="{span:15}" :xl="{span:17}" class="alert-area">
            <Alert>
              <template #icon>
                <i class="fas fa-cannabis"></i>
              </template>
              <template #title>
                <b>公告：</b>个人网站，感谢来访。
              </template>
              <template #desc>
                <span>如有问题可随时找站主交流，站主很闲的说。( •̀ ω •́ )✧</span>
              </template>
            </Alert>
          </Col>
          <Col :xs="{span:23}" :sm="{span:0}" :lg="{span:7}" :xl="{span:5}" style="height: 82px">
            <div class="weather-area">
              <Weather></Weather>
            </div>
          </Col>
        </Row>

        <Row type="flex" justify="center" align="top" class="home-body" style="padding-top:20px">
          <Col :xs="{span:24}" :sm="{span:23}" :lg="{span:15}" :xl="{span:17}">

            <Row type="flex" justify="center" align="top">
              <Col :xs="{span:24}" :sm="{span:24}" :lg="{span:24}" :xl="{span:12}" class="col-fall">
                <div class="mark-area">
                  <div class="title">
                    <span>博客&笔记专区</span>
                  </div>

                  <div class="content">
                    <div class="description">
                      <p>
                        这里会更新一些站主的博客与学习笔记，主要涉及django、drf、react、vue.js以及数据库优化、分布式、微服务、机器学习、
                        数据分析与挖掘等方面的文章。站主不是大牛大佬，写文也只是兴趣爱好（ps：初中开始就爱写小说，写博客也算是"不忘初心"了），
                        同时写文也能够帮助巩固记忆，其实效果还是挺不错的。文章不能保证满是干货，也不能保证技术拉满，但绝对用心，站主学习上的困惑
                        与经验都会一一记录， 若是能帮到大家，也算是一种收获。
                      </p>
                    </div>

                    <div class="total">
                      <div class="item">
                        <i class="fas fa-bookmark"></i>
                        <span>547</span>
                        <span>全站博文</span>
                      </div>
                      <div class="item">
                        <i class="fas fa-comments"></i>
                        <span>1547</span>
                        <span>总评论量</span>
                      </div>
                      <div class="item">
                        <i class="fas fa-eye"></i>
                        <span>15w+</span>
                        <span>总浏览量</span>
                      </div>
                      <div class="item">
                        <i class="fas fa-users"></i>
                        <span>1254</span>
                        <span>累计人次</span>
                      </div>
                    </div>

                    <div class="footer">
                      <button class="btn black">这里是传送门(ง •_•)ง</button>
                    </div>
                  </div>
                </div>

                <div class="mark-area">
                  <div class="title">
                    <span>隐藏专区</span>
                  </div>

                  <div class="content">
                    <div class="description">
                      <p>
                        正如标题所说，这里是本站的隐藏专区，非大会员vip是看不到这里的内容的。欸？！！你看得到么？
                        看得到的话那就要替我保密，我允许你浏览这其中的秘辛，但相应的，你不能把你看到的内容传播出去，你知我知即可，
                        好了，如此，我们之间的契约便达成了。<br/>
                        为什么叫隐藏专区呢，嗯，站主我可能会在这里偷偷写一些小说，小故事啥的，对你没听错，爱好这个东西，果真是不能轻易抛弃的。
                        不过我的文笔属实有些拉跨，情节的构造与展开也很难前后自洽，而且写作这个东西于我来说，就像辣条一样，吃的多了受不了，一段时间不吃却又馋得要死，
                        终究是无法抛弃掉的东西，那就索性把它也放进我的个人空间里吧。
                        我也不怕丢人了，写的不好也没啥，给大家一个乐呵。淦！<br/>
                        哦，另外值得一提的就是，可能轻小说一类的题材会偏多点。如果我能坚持下去写个十万八万字的话，就去轻小说站点投个稿，如果又是三天打鱼两天晒网，
                        那索性还是不要投出去丢人现眼了，毕竟之前写了十几章就收手的事情我没少做。
                      </p>
                    </div>

                    <div class="footer">
                      <button class="btn black">这里是传送门(ง •_•)ง</button>
                    </div>
                  </div>
                </div>
              </Col>

              <Col :xs="{span:24}" :sm="{span:24}" :lg="{span:24}" :xl="{span:12}" class="col-fall">
