                    <span>闲言闲语</span>
                  </div>

                  <div class="content">
                    <div class="description">
                      <p>
                        这里可能会更新一些动态、分享一下此刻的心情什么的，不过对于连qq动态、微信朋友圈都
                        懒得编辑的我来说，这可能并没有太大的可信度。不过先这样规划好了，不然这个网站首页的内容就
                        太空啦（；´д｀）ゞ。个人网站嘛，来来回回就那么些东西，我可以做的很小，但心想着多敲点代码，
                        就故意做的稍微正经一点，不过确实很多内容是空洞的就是了。以后慢慢填吧。
                      </p>
                    </div>

                    <div class="footer">
                      <button class="btn black">这里是传送门(ง •_•)ง</button>
                    </div>
                  </div>
                </div>

                <div class="mark-area">
                  <div class="title">
                    <span>资源分享专区</span>
                  </div>

                  <div class="content">
                    <div class="description">
                      <p>
                        站主如果找到一些帮助性很大的资源，可能会在这里分享，不只限于学习方面。
                        资源分享的话默认百度网盘，所有资源均收集自全网或站主自行整理，如有侵权，联系必删。
                      </p>
                    </div>

                    <div class="footer">
                      <button class="btn black">这里是传送门(ง •_•)ง</button>
                    </div>
                  </div>
                </div>

                <div class="mark-area">
                  <div class="title">
                    <span>关于本站</span>
                  </div>

                  <div class="content">
                    <div class="description">
                      <p>
                        你好，这里是【杨豪的个人网站】，小站自2021年4月29日上线，采用前后端分离的开发模式，后端框架采用了django以及django rest framework，
                        前端框架则采用了在国内非常流行的vue.js，ui框架使用了view-ui，不过本着磨练自己开发本领的想法，因此在开发过程中并没有太过依赖于ui框架，
                        只使用了其中极少的一部分组件，大部分组件均为自己封装实现。当然，由于是自己封装的东西，实用性、易用性、可复用性方面考虑的并不严谨，也不够老练，
                        老实说，很多细节的地方值得商榷，很多地方想法有，但技术上却不足以支撑实现，因此学习的地方尚还很多。
                        项目源码我在gitee和github上均有托管，如果有感兴趣的朋友可以自行查看，如果发现了一些代码上的不足之处，还请不吝赐教！
                      </p>
                    </div>

                    <div class="footer">
                      <button class="btn pyblue">传送至gitee</button>
                      <button class="btn black">传送至github</button>
                    </div>
                  </div>
                </div>
              </Col>

            </Row>
          </Col>

          <Col :xs="{span:24}" :sm="{span:23}" :lg="{span:7}" :xl="{span:5}" class="home-widget">
