              <Search></Search>
            </div>

            <div class="message-board">
              <MessageBoard></MessageBoard>
            </div>

            <div class="word-cloud">
              <WordCloud></WordCloud>
            </div>
          </Col>
        </Row>

      </div>
    </scroll>

  </div>
</template>

<script>
  import Alert from "components/common/widgets/Alert";
  import Weather from "components/common/weather/Weather";
  import Search from "components/common/form/Search";
  import MessageBoard from "components/common/widgets/MessageBoard";
  import WordCloud from "components/common/widgets/WordCloud";

  import Scroll from "components/common/Scroll/Scroll";


  export default {
    name: "Home",
    components:{
      Alert,
      MessageBoard,
      Weather,
      Search,
      WordCloud,
      Scroll,
    },
    data(){
      return{
        device_width:document.body.clientWidth || document.documentElement.clientWidth,
        listenScroll:true, //是否监听scroll
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll, true);
      this.$bus.$on('browserChange',(val) => {
        this.device_width = val;
      })
    },
    methods:{
      ContentScroll(position){
        /* 监听移动端touch滚动 */

      },
      handleScroll(){
        let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop;
      },
      pullUp(){
        console.log('pullup');
        this.$refs.scroll.finishPullUp();
      },
      pullDown(){
        console.log('pulldown');
        this.$refs.scroll.finishPullDown();
      },
    }
  }
</script>

<style scoped lang="less">
  @keyframes bgSlip {
    0%{
      background-color: rgba(120,120,120,0.4);
    }
    100%{
      width: 100%;
      background-color: #87d068;
    }

  }
  #home{
    //background-color: @color-lightwhite;
    background: url(../../assets/img/bg3.gif) repeat;
    height: 100%;
    width: 100%;
    position: relative;

    @media screen{
      @media (max-width: 568px){
        height: calc(100vh - 44px);
      }
      @media (min-width: 569px) and (max-width: 992px){
        height: calc(100vh - 56px);
        position: absolute;
        top: 44px;
      }
      @media (min-width: 993px) and (max-width: 1200px){
        height: calc(100vh - 56px);
        position: absolute;
        top: 44px;
      }

    }

    .wrapper{
      height: auto;
      @media screen{
        @media (max-width: 568px){
          position: absolute;
          top: 0;bottom: 0;
