              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: center;
              i{
                font-size: 24px;
                padding-bottom: 5px;
              }
            }
          }
        }
      }

      .collection{
        width: 100%;
        margin-bottom: 10px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .title{
          padding: 5px 20px;
