          font-size: 22px;
          letter-spacing: 2px;
          color: #660000;
          background-color: rgba(120,120,120,0.2);
          position: relative;
          overflow: hidden;
          z-index: 0;

          &:before {
            background: @color-white;
            content: "";
            position: absolute;
            z-index: -1;
            top: 0;
            right: auto;
            bottom: auto;
            left: 0;
            width: 0;
            height: 100%;
            opacity: 1;
            transition: all 0.4s ease-in-out;
          }

          &:hover{
            color: #87d068;
          }
          &:hover:before{
            width: 100%;
          }

          i{
            margin-right: 5px;
          }
        }

        .body{
          margin: 10px;
          width: 100%;
          overflow: hidden;
          padding: 20px 30px;
          background: @color-white;
          color: gray;
          border-radius: 10px;
          font-size: 16px;

          @media screen{
