              padding: 15px 8px;
              font-size: 12px;
            }
          }
        }
      }

    }

    .home-widget{
      margin-left: 10px;
      padding: 0 5px;
      @media screen{
        @media (max-width: 568px){
          margin: 0;
        }
      }

      .search,.message-board,.word-cloud{
