<template>
  <div class="search">
    <div class="important">
      <input class="input" type="text" placeholder="搜点什么吧~" @focus="popup" @blur="close"></input>
      <button class="search-btn">搜索</button>
    </div>

    <div :class="['search-tips',{'popup':isPopup}]">
      <span style="color: #0ACDC0">
        <i class="fas fa-feather" style="padding-right: 3px"></i> <b>最近更新</b>
      </span>
      <ItemCell v-for="item in tips" :key="item" :value="item"></ItemCell>
    </div>
  </div>
</template>

<script>
  import ItemCell from "../widgets/ItemCell";

  export default {
    name: "Search",
    components:{
      ItemCell
    },
    data(){
      return{
        isPopup:false,
        tips:[
          '博客1', '博客2', '博客3', '博客4', '博客5','博客6',
        ]
      }
    },
    methods:{
      popup(){
        this.$set(this.$data,'isPopup',true);
      },
      close(){
        this.$set(this.$data,'isPopup',false);
      }
    }
  }
</script>

<style scoped lang="less">
  .search{
    width: 95%;
    height: 40px;
    line-height: 40px;
    display: flex;
    flex-direction: column;
    position: relative;

    .important{
      display: flex;
      align-items: center;
      width: 100%;
      .input{
        width: 80%;
        height: 36px;
        padding: 0 10px;
        border-radius: 5px 0 0 5px;
        color: @color-muted;
        &:focus{
          border: @color-skyblue solid 1px;
        }
        &::placeholder{
          color: @color-muted;
        }
      }
      .search-btn{
        height: 36px;
        flex: 1;
        background: @color-skyblue;
        color: @color-white;
        border-radius: 0 5px 5px 0;
        cursor: pointer;
        letter-spacing: 2px;
      }
    }

    .search-tips{
      height: 0;
      overflow: hidden;
    }
    .popup{
      position: absolute;
      top: 40px;
      width: 100%;
      margin-top: 10px;
      height: 270px;
      padding: 0 10px;
      transition: height ease 0.6s;
      background: @color-white;
      border: @color-border solid 1px;
      border-radius: 5px;
      z-index: 99;
    }
  }
</style>