<template>
  <div id="weather">
    <div class="data">
      <div class="icon">
        <i class="fas fa-snowflake"></i>
      </div>
      <span class="tmp">28°C</span>
    </div>

    <button class="add">河南省 开封市 河南大学金明校区</button>

  </div>
</template>

<script>
  /* 展示天气的组件，具体展示的选择为：（0，晴天）、（1，多云）、（2，阴雨）、（3，夜晚）、（4，雷雨）、（5，雪天） */
  /* 晴天：sunny 多云：cloudy 阴雨：rainy 夜晚：starry 雷雨：stormy 雪天：snowy */
  export default {
    name: "Weather",
    props:{
      weather:{
        type:Number,
        default:0
      }
    },
    computed:{
      computedWeather(){
        if(this.weather === 0){
          return 'sunny';
        }else if(this.weather === 1){
          return 'cloudy';
        }else if(this.weather === 2){
          return 'rainy';
        }else if(this.weather === 3){
          return 'starry';
        }else if(this.weather === 4){
          return 'stormy';
        }else if(this.weather === 5){
          return 'snowy';
        }
      }
    }
  }
</script>

<style scoped lang="less">
 #weather{
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   width: 100%;height: 100%;
   background-color: @color-skyblue;
   border-radius: 5px;
   padding: 5px 0px;
   .data{
     display: flex;
     justify-content: center;
     align-items: center;
     .icon{
       i{
         font-size: 26px;
         color: @color-white;
         @media screen{
           @media (max-width: 768px){
             font-size: 22px;
           }
         }
       }
     }
     .tmp{
       font-size: 28px;
       color: @color-white;
       padding-left: 10px;
       @media screen{
         @media (max-width: 768px){
           font-size: 22px;
         }
       }
     }
   }
   .add{
     background-color: rgba(30,30,30,0.3);
     border-radius: 15px;
     padding: 5px 18px;
     color: @color-white;
     font-size: 12px;
     cursor: pointer;
     @media screen{
       @media (max-width: 768px){
         font-size: 10px;
       }
     }
   }
 }
</style>